import {AuthLayout} from "../layouts/authLayout";
import "../styles/Profile.scss"

export const Profile = () => {
    return (
        <AuthLayout>
            <div className="container__form">
                <div id="authenticated-block" className="authenticated-block">
                    <h2 className="authenticated-block__title"> </h2>
                    <p className="authenticated-block__text">     <span></span></p>
                    <p className="authenticated-block__text"> : <span></span> 
                    </p>
                    <button className="authenticated-block__button button-red"></button>
                </div>
            </div>
        </AuthLayout>
    )
}